    margin-left: 50%;
    align-self: center;
}
.forecast_loader__1kfwO:after {
    content: " ";
    display: block;
    width: 100px;
    height: 100px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #2c4357;
    border-color: #2c4357 transparent #2c4357 transparent;
    -webkit-animation: forecast_lds-dual-ring__c5SSb 1.2s linear infinite;
            animation: forecast_lds-dual-ring__c5SSb 1.2s linear infinite;

}
@-webkit-keyframes forecast_lds-dual-ring__c5SSb {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes forecast_lds-dual-ring__c5SSb {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

